.page {
  margin: 0;
  font-family: $main-font-family;
  color: $main-text-color;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;

    @include on-desktop {
      position: relative;
      z-index: -2;
    }

    &:has(.page__menu:target) {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transition: all $time-transform;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

.container {
  @include content-padding-inline;
}
